import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { User } from "oidc-client";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import { ItemList } from "../dataContracts/ItemList";
import ApiService from "../services/ApiService";
import ItemListSlot from "./ItemListSlot";

export type SidebarProps = {

}

export type SidebarState = {
    user: User | undefined;
    itemLists: ItemList[];
}

export default class Sidebar extends React.Component<SidebarProps, SidebarState> {
    constructor(props: SidebarProps) {
        super(props);

        this.state = {
            user: undefined,
            itemLists: []
        };

        this.refreshItemLists = this.refreshItemLists.bind(this);
    }

    async refreshItemLists() {
        const { user } = this.state;
        if (!user)
            return;

        const itemLists = await ApiService.GetItemLists(user.access_token);
        if (!itemLists)
            return;

        this.setState({itemLists});
    }

    render() {
        const { itemLists, user } = this.state;

        return(
            <div className="sidebar hidden py-4 xl:flex xl:w-1/4 bg-gray-900" style={{width: "16rem"}}>
                <AuthenticationContext.Consumer>
                    {props => {
                        if (props.oidcUser != null && !this.state.user)
                            this.setState({user: props.oidcUser}, () => {
                                this.refreshItemLists();
                            });

                        return(
                            <Scrollbar noScrollX>
                                {itemLists.map((itemList, index) => (
                                    <div key={index} className="mb-2">
                                        <ItemListSlot itemList={itemList} accessToken={user?.access_token ?? ""} />
                                    </div>
                                ))}
                            </Scrollbar>
                        );
                    }}
                </AuthenticationContext.Consumer>
            </div>
        );
    }
}