import { AuthenticationContext } from "@axa-fr/react-oidc-context";
import { User } from "oidc-client";
import React from "react";
import Scrollbar from "react-scrollbars-custom";
import ItemSummarySlot from "../components/ItemSummarySlot";
import SortingBar from "../components/SortingBar";
import { PricedItem } from "../dataContracts/PricedItem";
import { ItemRarity } from "../utils/ItemRarity";
import { ItemType } from "../utils/ItemType";

export type GeneralSearchProps = {

}

export type GeneralSearchState = {
    user: User | undefined;
}

export default class GeneralSearch extends React.Component<GeneralSearchProps, GeneralSearchState> {
    constructor(props: GeneralSearchProps) {
        super(props);

        this.state = {
            user: undefined
        };
    }

    render() {
        const { user } = this.state;

        const item: PricedItem = {
            Id: 27328,
            ChatLink: "",
            Name: "Ebonblade",
            Description: "",
            Icon: "https://render.guildwars2.com/file/F7B7ECFA56A55434245E4D311516A2C7771204B4/780577.png",
            Type: ItemType.Weapon,
            Rarity: ItemRarity.Exotic,
            Level: 0,
            Sold: 0,
            Bought: 0,
            Margin: 0,
            Appeal: 0,
            BuyPrice: 0,
            SellPrice: 0
        };

        return(
            <div className="main w-full px-4 py-4 bg-gray-800 text-gray-200">
                <AuthenticationContext.Consumer>
                    {props => {
                        if (props.oidcUser != null && !this.state.user)
                            this.setState({user: props.oidcUser});

                        if (user) {
                            return(
                                <SortingBar name="Results">
                                    {sortedItems => (
                                        <Scrollbar noScrollX style={{height: "calc(100vh - 11rem)"}}>
                                            {sortedItems.map((value, index) => (
                                                <div key={index} className="mb-2">
                                                    <ItemSummarySlot item={value} accessToken={user.access_token} />
                                                </div>
                                            ))}
                                        </Scrollbar>
                                    )}
                                </SortingBar>
                            );
                        }

                        return(<></>);
                    }}
                </AuthenticationContext.Consumer>
            </div>
        );
    }
}