import React from "react";
import { Link } from "react-router-dom";
import { ItemList } from "../dataContracts/ItemList";
import environment from "../environment";
import ApiService from "../services/ApiService";

export type ItemListSlotProps = {
    itemList: ItemList;
    accessToken: string;
}

export type ItemListSlotState = {
    isHover: boolean;
}

export default class ItemListSlot extends React.Component<ItemListSlotProps, ItemListSlotState> {
    constructor(props: ItemListSlotProps) {
        super(props);

        this.state = {
            isHover: false
        };

        this.onDragEnter = this.onDragEnter.bind(this);
        this.onDragLeave = this.onDragLeave.bind(this);
        this.onDrop = this.onDrop.bind(this);
    }

    async onDragEnter(event: React.DragEvent<HTMLAnchorElement>) {
        event.preventDefault();
        this.setState({isHover: true});
    }

    async onDragLeave(event: React.DragEvent<HTMLAnchorElement>) {
        event.preventDefault();
        this.setState({isHover: false});
    }

    async onDrop(event: React.DragEvent<HTMLAnchorElement>) {
        event.preventDefault();

        const { itemList, accessToken } = this.props;
        const itemId = parseInt(event.dataTransfer.getData("text"));
        await ApiService.AddListItem(accessToken, itemList.id, itemId);
    }

    render() {
        const { itemList } = this.props;
        const { isHover } = this.state;

        return(
            <Link className={`transition duration-150 ease-in-out block px-5 py-3 bg-opacity-25 ${isHover ? "bg-green-700 text-green-300" : "bg-indigo-700 text-indigo-300"}  hover:bg-opacity-100 hover:text-indigo-200 cursor-pointer`}
                to={`/list/${itemList.id}`}
                onDragEnter={this.onDragEnter}
                onDragLeave={this.onDragLeave}
                onDrop={this.onDrop}>
                <span className="">{itemList.name}</span>
            </Link>
        );
    }
}